<template>
  <div class="detail" v-if="detail">
    <h4>名字</h4>
    <p>{{detail.name}}</p>
    <h4>库存数量</h4>
    <p>{{detail.num}}</p>
    <h4>更新时间</h4>
    <p>{{detail.time | formatDate}}</p>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  created () {
    this.$store.dispatch('getFruitData')
  },
  computed: {
    curId(){
      return this.$route.query.id ? this.$route.query.id : 0;
    },
    detail() {
      if(!this.$store.state.fruitData.length) return null
      return this.$store.state.fruitData[this.curId]
    }
  }
}
</script>
<style lang="less" scoped>
h4{
  color: #80848F;
}
</style>



